nav { 
    background-color: rgb(0, 255, 183);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: .5rem 0;
    box-shadow: 4px 4px 8px #00000020;
}

nav > svg {
    height: 3rem;
    width: auto;
}

.switch {
    display: flex;
    align-items: center;
    gap: 10px;
}

.switch > svg {
    height: 1.5rem;
    width: auto;
}

.switch > label {
    background-color: #ccc;
    width: 4rem;
    height: 1.5rem;
    border-radius: 1rem;
    cursor: pointer;   
    display: flex;
    align-items: center;
    padding: 2px;
}

.switch > label > span {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 1.5rem;
    background-color: #fff;
    transition: 0.5s;
}

.check-switch:checked + .slider {
    transform: translateX(2.5rem);
} 