.titulo{
    font-size: 36px;
    font-weight: 400;
    color: #000;
    text-align: center;
    margin: 2rem 0 1rem 0;
}

.container-buscar {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 5px 0;
}

.input-buscar {
    background-color: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 12px;
    flex: .4;
    font-size: 16px;
    box-shadow: 4px 4px 16px #00000020;
}

.btn-buscar{ 
    background-color: #26e;
    color: #f2f2f2;
    border-radius: 10px;
    padding: 12px;
    border: none;
    box-shadow: 4px 4px 16px #00000020;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-size: 16px;
    transition: 0.4s;
}

.btn-buscar > svg {
    width: 18px;
    fill: #f2f2f2;
}

.btn-buscar:hover {
    transform: scale(1.1);
    opacity: 0.9;
}